
/**
 * @imports
 */
import _each from '@web-native-js/commons/obj/each.js';
import SCOPED_HTML_ENV from './scoped-html/ENV.js';
import SCOPED_JS_ENV from './scoped-js/ENV.js';
import CHTML_ENV from './chtml/ENV.js';

/**
 * Configures all ENVs with common values.
 * 
 * @param object COMMON_ENV
 * 
 * @return void
 */
export default function ENV_ALL(commons) {
    _each(commons, (property, value) => {
        SCOPED_HTML_ENV[property] = value;
        SCOPED_JS_ENV[property] = value;
        CHTML_ENV[property] = value;
    });
};

// Individual Access
ENV_ALL.ScopedHTML = SCOPED_HTML_ENV;
ENV_ALL.ScopedJS = SCOPED_JS_ENV;
ENV_ALL.Chtml = CHTML_ENV;
// Default common Trap
ENV_ALL.Chtml.ScopedHTML = SCOPED_HTML_ENV;
ENV_ALL.Chtml.ScopedJS = SCOPED_JS_ENV;
